.backdrop {
  position: fixed;
  inset: 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 999;
}

.backdrop.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.modal {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
}

